export default {
	data () {
		return {
			user: {}, // 用户信息
			payments: [], // 可用充值方式列表
			money: '', // 充值的金额
			orderType: 2	// 充值类型
		}
	},
	onLoad () {
		this.userInfo()
	},
	methods: {
		// 获取用户信息
		userInfo () {
			this.$api.userInfo({}, res => {
				if (res.status) {
					this.user = res.data
				}
			})
		},
		// 去充值
		navigateToHandle () {
			if (!Number(this.money)) {
				this.$common.errorToShow('请输入要充值的金额')
			} else {
				this.$common.navigateTo('/pages/goods/payment/index?recharge=' + Number(this.money) + '&type=' + this.orderType)
			}
		}
	}
}
